.fullheight {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  .inner-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 300px;
  }
}

.code {
  border: 1px solid #fff;
  padding: 1rem 0rem 1rem 1rem;
  font-size: 2rem;
  letter-spacing: 1rem;
  margin-bottom: 2rem;
}
